import { BulkDeleteActionParams } from './types';
import { Delete } from '@wix/wix-ui-icons-common';
import React from 'react';

export const bulkDeleteAction = (params: BulkDeleteActionParams) => {
  const { action, sdk, actionParams } = params;
  return {
    onClick: () => {
      if (action.bulkDelete.mode === 'modal') {
        sdk.openModal('BulkDeleteModal', {
          modalParams: action.bulkDelete.modal,
          actionParams,
          sdk,
        });
      }
    },
    label: 'Delete',
    icon: <Delete />,
  };
};
